"use client";

import { useEffect, useState } from "react";
import { useAwsCredentials } from "@/hooks/useAwsCredentials";
import { 
  AlertCircle, 
  AlertTriangle, 
  Check, 
  CheckCircle, 
  ChevronDown, 
  ChevronUp, 
  CircleAlert, 
  Database, 
  FileQuestion,
  FileText, 
  Key, 
  Lock,
  RefreshCw, 
  Shield, 
  ShieldAlert,
  User, 
  UserCog,
  Users,
  AlertOctagon,
  ShieldCheck
} from "lucide-react";
import CredentialSelector from "@/components/aws/CredentialSelector";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { 
  Alert,
  AlertTitle,
  AlertDescription
} from "@/components/ui/alert";
import { Badge } from "@/components/ui/badge";
import { Spinner } from "@/components/ui/spinner";

// Helper function to format date
function formatDate(date: Date | string | undefined): string {
  if (!date) return 'N/A';
  
  try {
    const dateObj = new Date(date);
    if (isNaN(dateObj.getTime())) return 'N/A';
    
    const now = new Date();
    const diffMs = now.getTime() - dateObj.getTime();
    const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));
    
    if (diffDays === 0) {
      return 'Today';
    } else if (diffDays === 1) {
      return 'Yesterday';
    } else if (diffDays < 30) {
      return `${diffDays} days ago`;
    } else if (diffDays < 365) {
      const months = Math.floor(diffDays / 30);
      return `${months} ${months === 1 ? 'month' : 'months'} ago`;
    } else {
      const years = Math.floor(diffDays / 365);
      return `${years} ${years === 1 ? 'year' : 'years'} ago`;
    }
  } catch (e) {
    console.error("Error formatting date:", e);
    return 'N/A';
  }
}

// Define interfaces for IAM users
interface IAMUser {
  id: string;
  userName: string;
  arn: string;
  createDate: string;
  passwordLastUsed?: string;
  lastActivity?: string;
  hasConsoleAccess: boolean;
  hasAccessKeys: boolean;
  hasMFA: boolean;
}

// Define interfaces for security issues
interface SecurityIssue {
  id?: string;
  title: string;
  description: string;
  severity?: string;
  recommendation?: string;
  userName?: string;
  policyName?: string;
  roleName?: string;
  accessKeyId?: string;
}

interface RootAccessIssue extends SecurityIssue {}

interface UserIssue extends SecurityIssue {
  user: string;
}

interface PolicyIssue extends SecurityIssue {
  policyName: string;
  policyArn: string;
}

interface AccessKeyIssue extends SecurityIssue {
  user: string;
  accessKeyId: string;
}

interface PasswordPolicyIssue extends SecurityIssue {}

interface IAMSecurityData {
  rootAccountIssues: SecurityIssue[];
  userIssues: SecurityIssue[];
  policyIssues: SecurityIssue[];
  roleIssues: SecurityIssue[];
  accessKeyIssues: SecurityIssue[];
  passwordPolicyIssues: SecurityIssue[];
}

// Updated interfaces for IAM security data from our new API
interface IAMUserIssue {
  type: 'NO_MFA' | 'UNUSED_ACCOUNT' | 'OLD_ACCESS_KEY' | 'EXCESSIVE_PERMISSIONS' | 'NO_PASSWORD_ROTATION';
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH' | 'CRITICAL';
}

interface IAMUserSecurityData {
  userName: string;
  userId: string;
  arn: string;
  createDate: Date;
  passwordLastUsed?: Date;
  hasConsoleAccess: boolean;
  hasAccessKeys: boolean;
  hasMFA: boolean;
  issues: IAMUserIssue[];
}

interface IAMPolicyIssue {
  type: 'OVERLY_PERMISSIVE' | 'UNUSED_POLICY' | 'ADMIN_POLICY';
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH';
}

interface IAMPolicySecurityData {
  policyName: string;
  policyId: string;
  arn: string;
  description?: string;
  isAttached: boolean;
  isAWSManaged: boolean;
  createDate: Date;
  updateDate?: Date;
  issues: IAMPolicyIssue[];
}

interface IAMGroupIssue {
  type: 'EMPTY_GROUP' | 'EXCESSIVE_PERMISSIONS';
  description: string;
  severity: 'LOW' | 'MEDIUM';
}

interface IAMGroupSecurityData {
  groupName: string;
  groupId: string;
  arn: string;
  createDate: Date;
  memberCount: number;
  issues: IAMGroupIssue[];
}

interface IAMRoleIssue {
  type: 'OVERLY_PERMISSIVE' | 'UNUSED_ROLE' | 'ADMIN_POLICY';
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH';
}

interface IAMRoleSecurityData {
  roleName: string;
  roleId: string;
  arn: string;
  createDate: Date;
  description?: string;
  serviceLinked: boolean;
  issues: IAMRoleIssue[];
}

interface AccessKeyIssue {
  type: 'INACTIVE_KEY' | 'OLD_KEY' | 'UNUSED_KEY';
  description: string;
  severity: 'LOW' | 'MEDIUM' | 'HIGH';
}

interface AccessKeySecurityData {
  accessKeyId: string;
  userName: string;
  status: string;
  createDate: Date;
  lastUsed?: Date;
  issues: AccessKeyIssue[];
}

interface AccountIssue {
  type: 'ROOT_ACCESS_KEY' | 'NO_MFA_ROOT' | 'PASSWORD_POLICY' | 'EXCESS_IAM_USERS';
  description: string;
  severity: 'MEDIUM' | 'HIGH' | 'CRITICAL';
}

// New interface for IAM security summary
interface IAMSecuritySummary {
  users: IAMUserSecurityData[];
  policies: IAMPolicySecurityData[];
  groups: IAMGroupSecurityData[];
  roles: IAMRoleSecurityData[];
  accessKeys: AccessKeySecurityData[];
  accountIssues: AccountIssue[];
}

// New interface for API response
interface IAMSecurityResponse {
  securityData: IAMSecuritySummary;
  issueCount: {
    total: number;
    bySeverity: {
      critical: number;
      high: number;
      medium: number;
      low: number;
    };
    byCategory: {
      users: number;
      policies: number;
      groups: number;
      roles: number;
      accessKeys: number;
      accountSettings: number;
    };
  };
}

// Security Summary Card Component
const SecuritySummaryCard = ({ 
  title, 
  count, 
  icon, 
  variant = "default" 
}: { 
  title: string; 
  count: number; 
  icon: React.ReactNode; 
  variant?: "default" | "destructive" | "success" 
}) => {
  const variantClasses = {
    default: "bg-gray-100 text-gray-800",
    destructive: "bg-red-100 text-red-800",
    success: "bg-green-100 text-green-800"
  };

  const getVariantClass = () => {
    if (count === 0) return variantClasses.success;
    return count > 0 ? variantClasses.destructive : variantClasses.default;
  };

  return (
    <div className={`p-4 rounded-lg ${getVariantClass()} flex items-center space-x-4`}>
      <div className="p-2 rounded-full bg-white">
        {icon}
      </div>
      <div>
        <h3 className="font-medium">{title}</h3>
        <p className="text-2xl font-bold">{count}</p>
      </div>
    </div>
  );
};

// Main IAM page component
export default function IAMPage() {
  const { selectedCredential, selectCredential } = useAwsCredentials();
  const [users, setUsers] = useState<IAMUser[]>([]);
  const [securityData, setSecurityData] = useState<IAMSecurityResponse | null>(null);
  const [activeTab, setActiveTab] = useState("users");
  const [loading, setLoading] = useState(false);
  const [securityLoading, setSecurityLoading] = useState(false);
  const [error, setError] = useState("");
  const [securityError, setSecurityError] = useState("");
  
  // Expanded sections for security tab
  const [expandedSections, setExpandedSections] = useState({
    users: true,
    policies: false,
    groups: false,
    roles: false,
    accessKeys: false,
    accountSettings: false
  });

  // Function to fetch IAM users
  const fetchIAMUsers = async (credentialId: string) => {
    setLoading(true);
    setError("");
    
    try {
      const response = await fetch(`/api/aws/iam/users?credentialId=${credentialId}`);
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || "Failed to fetch IAM users");
      }
      
      const data = await response.json();
      setUsers(data.users);
    } catch (err) {
      console.error("Error fetching IAM users:", err);
      setError(err instanceof Error ? err.message : "An unknown error occurred");
    } finally {
      setLoading(false);
    }
  };

  // Function to fetch security data
  const fetchSecurityData = async (credentialId: string) => {
    setSecurityLoading(true);
    setSecurityError("");
    
    try {
      const response = await fetch(`/api/aws/iam/security?credentialId=${credentialId}`);
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || "Failed to fetch security data");
      }
      
      const data = await response.json();
      setSecurityData(data);
    } catch (err) {
      console.error("Error fetching security data:", err);
      setSecurityError(err instanceof Error ? err.message : "An unknown error occurred");
    } finally {
      setSecurityLoading(false);
    }
  };

  // Function to toggle expanded sections
  const toggleSection = (section: keyof typeof expandedSections) => {
    setExpandedSections(prev => ({
      ...prev,
      [section]: !prev[section]
    }));
  };

  // Fetch data when selected credential changes
  useEffect(() => {
    if (selectedCredential) {
      fetchIAMUsers(selectedCredential.id);
      
      if (activeTab === "security") {
        fetchSecurityData(selectedCredential.id);
      }
    } else {
      setUsers([]);
      setSecurityData(null);
    }
  }, [selectedCredential, activeTab]);

  // Handle tab change
  const handleTabChange = (value: string) => {
    setActiveTab(value);
    
    if (value === "security" && selectedCredential && !securityData) {
      fetchSecurityData(selectedCredential.id);
    }
  };

  // Handle refresh
  const handleRefresh = () => {
    if (!selectedCredential) return;
    
    if (activeTab === "users") {
      fetchIAMUsers(selectedCredential.id);
    } else if (activeTab === "security") {
      fetchSecurityData(selectedCredential.id);
    }
  };

  return (
    <div className="container mx-auto py-6 space-y-6">
      {/* Header */}
      <div className="flex flex-col space-y-2 md:flex-row md:justify-between md:items-center">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">IAM Security</h1>
          <p className="text-gray-500">
            Monitor users, access keys, and security issues for IAM
          </p>
        </div>
        
        <div className="flex items-center space-x-4">
          <button 
            onClick={handleRefresh}
            className="px-3 py-2 border rounded-md hover:bg-gray-100"
          >
            <RefreshCw className="h-4 w-4" />
            <span className="sr-only">Refresh</span>
          </button>
        </div>
      </div>

      <CredentialSelector />

      {/* Main Content */}
      <div className="space-y-4">
        {!selectedCredential ? (
          <div className="p-4 bg-yellow-50 border-l-4 border-yellow-400 text-yellow-700">
            <div className="flex">
              <AlertCircle className="h-5 w-5 mr-2" />
              <div>
                <h3 className="font-medium">No credential selected</h3>
                <p>Please select an AWS credential to view IAM information</p>
              </div>
            </div>
          </div>
        ) : (
          <div className="space-y-4">
            {/* Tabs */}
            <div className="border-b border-gray-200">
              <div className="flex -mb-px space-x-8">
                <button
                  onClick={() => handleTabChange("users")}
                  className={`flex items-center gap-2 py-4 px-1 border-b-2 font-medium text-sm ${
                    activeTab === "users"
                      ? "border-indigo-500 text-indigo-600"
                      : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                  }`}
                >
                  <Users className="h-4 w-4" />
                  <span>Users</span>
                </button>
                <button
                  onClick={() => handleTabChange("security")}
                  className={`flex items-center gap-2 py-4 px-1 border-b-2 font-medium text-sm ${
                    activeTab === "security"
                      ? "border-indigo-500 text-indigo-600"
                      : "border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300"
                  }`}
                >
                  <Shield className="h-4 w-4" />
                  <span>Security</span>
                </button>
              </div>
            </div>

            {/* Tab Content */}
            <div className="mt-4">
              {activeTab === "users" && (
                <UsersTab
                  users={users}
                  loading={loading}
                  error={error}
                  selectedCredential={selectedCredential.id}
                />
              )}
              {activeTab === "security" && (
                <SecurityTab 
                  securityData={securityData} 
                  loading={securityLoading} 
                  error={securityError}
                  selectedCredential={selectedCredential}
                  expandedSections={expandedSections}
                  toggleSection={toggleSection}
                />
              )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

// Users Tab Component
function UsersTab({ 
  users, 
  loading, 
  error, 
  selectedCredential 
}: { 
  users: IAMUser[]; 
  loading: boolean; 
  error: string; 
  selectedCredential: string; 
}) {
  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <Spinner size="xl" />
        <span className="ml-2">Carregando usuários...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-4 bg-red-50 border-l-4 border-red-400 text-red-700">
        <div className="flex">
          <AlertCircle className="h-5 w-5 mr-2" />
          <div>
            <h3 className="font-medium">Error</h3>
            <p>{error}</p>
          </div>
        </div>
      </div>
    );
  }

  if (!users.length) {
    return (
      <div className="p-4 bg-blue-50 border-l-4 border-blue-400 text-blue-700">
        <div className="flex">
          <AlertCircle className="h-5 w-5 mr-2" />
          <div>
            <h3 className="font-medium">No users found</h3>
            <p>No IAM users were found for the selected AWS credential.</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
        <div className="bg-white border rounded-lg p-4 shadow-sm">
          <div className="pb-2">
            <h3 className="text-lg font-medium">Total Users</h3>
          </div>
          <div className="text-3xl font-bold">{users.length}</div>
        </div>
        <div className="bg-white border rounded-lg p-4 shadow-sm">
          <div className="pb-2">
            <h3 className="text-lg font-medium">Without MFA</h3>
          </div>
          <div className="text-3xl font-bold text-orange-500">
            {users.filter(user => user.hasConsoleAccess && !user.hasMFA).length}
          </div>
        </div>
        <div className="bg-white border rounded-lg p-4 shadow-sm">
          <div className="pb-2">
            <h3 className="text-lg font-medium">With Access Keys</h3>
          </div>
          <div className="text-3xl font-bold">
            {users.filter(user => user.hasAccessKeys).length}
          </div>
        </div>
      </div>

      <div className="rounded-md border">
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead>
              <tr className="bg-gray-100">
                <th className="whitespace-nowrap px-4 py-3 text-left font-medium">Username</th>
                <th className="whitespace-nowrap px-4 py-3 text-left font-medium">Created</th>
                <th className="whitespace-nowrap px-4 py-3 text-left font-medium">Last Activity</th>
                <th className="whitespace-nowrap px-4 py-3 text-left font-medium">Console Access</th>
                <th className="whitespace-nowrap px-4 py-3 text-left font-medium">MFA</th>
                <th className="whitespace-nowrap px-4 py-3 text-left font-medium">Access Keys</th>
              </tr>
            </thead>
            <tbody>
              {users.map((user) => (
                <tr key={user.id} className="border-t">
                  <td className="whitespace-nowrap px-4 py-3 font-medium">{user.userName}</td>
                  <td className="whitespace-nowrap px-4 py-3">
                    {formatDate(user.createDate)}
                  </td>
                  <td className="whitespace-nowrap px-4 py-3">
                    {formatDate(user.passwordLastUsed)}
                  </td>
                  <td className="whitespace-nowrap px-4 py-3">
                    {user.hasConsoleAccess ? (
                      <Check className="h-5 w-5 text-green-500" />
                    ) : (
                      <span className="text-gray-500">—</span>
                    )}
                  </td>
                  <td className="whitespace-nowrap px-4 py-3">
                    {user.hasConsoleAccess ? (
                      user.hasMFA ? (
                        <Check className="h-5 w-5 text-green-500" />
                      ) : (
                        <AlertCircle className="h-5 w-5 text-orange-500" />
                      )
                    ) : (
                      <span className="text-gray-500">—</span>
                    )}
                  </td>
                  <td className="whitespace-nowrap px-4 py-3">
                    {user.hasAccessKeys ? (
                      <Check className="h-5 w-5 text-green-500" />
                    ) : (
                      <span className="text-gray-500">—</span>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

// Security Tab Component
function SecurityTab({ 
  securityData, 
  loading, 
  error,
  selectedCredential,
  expandedSections,
  toggleSection
}: { 
  securityData: IAMSecurityResponse | null; 
  loading: boolean; 
  error: string;
  selectedCredential: any;
  expandedSections: {
    users: boolean;
    policies: boolean;
    groups: boolean;
    roles: boolean;
    accessKeys: boolean;
    accountSettings: boolean;
  };
  toggleSection: (section: "users" | "policies" | "groups" | "roles" | "accessKeys" | "accountSettings") => void;
}) {
  if (!selectedCredential) {
    return (
      <Alert>
        <AlertTitle>Selecione uma credencial AWS</AlertTitle>
        <AlertDescription>
          Selecione uma credencial AWS para verificar problemas de segurança do IAM.
        </AlertDescription>
      </Alert>
    );
  }

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <Spinner size="xl" />
        <span className="ml-2">Analisando segurança IAM...</span>
      </div>
    );
  }

  if (error) {
    return (
      <Alert variant="destructive">
        <AlertCircle className="h-4 w-4" />
        <AlertTitle>Erro ao verificar segurança</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  if (!securityData) {
    return (
      <Alert>
        <AlertTitle>Dados de segurança não disponíveis</AlertTitle>
        <AlertDescription>
          Os dados de segurança do IAM ainda não foram carregados. Tente atualizar a página.
        </AlertDescription>
      </Alert>
    );
  }

  // Get issue counts for summary
  const { issueCount } = securityData;
  
  // Map severity to our UI variants
  const getSeverityVariant = (severity: string): "default" | "destructive" | "warning" => {
    switch(severity.toUpperCase()) {
      case 'CRITICAL':
      case 'HIGH':
        return "destructive";
      case 'MEDIUM':
        return "warning";
      default:
        return "default";
    }
  };

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <SecuritySummaryCard
          title="Total de Problemas"
          count={issueCount.total}
          icon={<AlertOctagon className="h-5 w-5" />}
          variant={issueCount.total > 0 ? "destructive" : "success"}
        />
        <SecuritySummaryCard
          title="Críticos e Altos"
          count={issueCount.bySeverity.critical + issueCount.bySeverity.high}
          icon={<AlertCircle className="h-5 w-5" />}
          variant={(issueCount.bySeverity.critical + issueCount.bySeverity.high) > 0 ? "destructive" : "success"}
        />
        <SecuritySummaryCard
          title="Médios e Baixos"
          count={issueCount.bySeverity.medium + issueCount.bySeverity.low}
          icon={<AlertTriangle className="h-5 w-5" />}
          variant="default"
        />
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-4">
        <SecuritySummaryCard
          title="Usuários"
          count={issueCount.byCategory.users}
          icon={<User className="h-5 w-5" />}
          variant={issueCount.byCategory.users > 0 ? "destructive" : "success"}
        />
        <SecuritySummaryCard
          title="Perfis (Roles)"
          count={issueCount.byCategory.roles}
          icon={<ShieldCheck className="h-5 w-5" />}
          variant={issueCount.byCategory.roles > 0 ? "destructive" : "success"}
        />
        <SecuritySummaryCard
          title="Políticas"
          count={issueCount.byCategory.policies}
          icon={<FileText className="h-5 w-5" />}
          variant={issueCount.byCategory.policies > 0 ? "destructive" : "success"}
        />
        <SecuritySummaryCard
          title="Chaves de Acesso"
          count={issueCount.byCategory.accessKeys}
          icon={<Key className="h-5 w-5" />}
          variant={issueCount.byCategory.accessKeys > 0 ? "destructive" : "success"}
        />
        <SecuritySummaryCard
          title="Conta"
          count={issueCount.byCategory.accountSettings}
          icon={<Shield className="h-5 w-5" />}
          variant={issueCount.byCategory.accountSettings > 0 ? "destructive" : "success"}
        />
      </div>
      
      {/* Detailed sections for each issue type */}
      <div className="space-y-4">
        {/* Users section */}
        <div className="border rounded-lg overflow-hidden">
          <button
            className="w-full p-4 bg-gray-50 flex justify-between items-center"
            onClick={() => toggleSection('users')}
          >
            <div className="flex items-center space-x-2">
              <User className="h-5 w-5" />
              <h3 className="text-lg font-medium">Usuários IAM</h3>
              {securityData.securityData.users && securityData.securityData.users.some(user => user.issues.length > 0) && (
                <Badge variant="destructive" className="ml-2">
                  {securityData.securityData.users.filter(user => user.issues.length > 0).length}
                </Badge>
              )}
            </div>
            <ChevronDown className={`h-5 w-5 transition-transform ${expandedSections.users ? 'rotate-180' : ''}`} />
          </button>
          {expandedSections.users && (
            <div className="p-4">
              <div className="space-y-4">
                {securityData.securityData.users
                  .filter(user => user.issues.length > 0)
                  .map((user, index) => (
                    <Card key={index} className="border-l-4 border-red-500">
                      <CardHeader className="pb-2">
                        <div className="flex justify-between items-start">
                          <CardTitle className="text-base">{user.userName}</CardTitle>
                          <Badge variant={getSeverityVariant(user.issues[0].severity)}>
                            {user.issues[0].severity}
                          </Badge>
                        </div>
                      </CardHeader>
                      <CardContent>
                        <p>{user.issues[0].description}</p>
                        {user.issues.length > 1 && (
                          <p className="text-sm text-gray-600 mt-2">
                            +{user.issues.length - 1} {user.issues.length > 2 ? 'outros problemas' : 'outro problema'}
                          </p>
                        )}
                      </CardContent>
                    </Card>
                  ))}
                {securityData.securityData.users.every(user => user.issues.length === 0) && (
                  <p className="text-green-600 flex items-center">
                    <CheckCircle className="h-4 w-4 mr-2" />
                    Nenhum problema encontrado com usuários IAM
                  </p>
                )}
              </div>
            </div>
          )}
        </div>
        
        {/* Roles section */}
        <div className="border rounded-lg overflow-hidden">
          <button
            className="w-full p-4 bg-gray-50 flex justify-between items-center"
            onClick={() => toggleSection('roles')}
          >
            <div className="flex items-center space-x-2">
              <ShieldCheck className="h-5 w-5" />
              <h3 className="text-lg font-medium">Perfis IAM (Roles)</h3>
              {securityData.securityData.roles && securityData.securityData.roles.some(role => role.issues.length > 0) && (
                <Badge variant="destructive" className="ml-2">
                  {securityData.securityData.roles.filter(role => role.issues.length > 0).length}
                </Badge>
              )}
            </div>
            <ChevronDown className={`h-5 w-5 transition-transform ${expandedSections.roles ? 'rotate-180' : ''}`} />
          </button>
          {expandedSections.roles && (
            <div className="p-4">
              <div className="space-y-4">
                {securityData.securityData.roles
                  .filter(role => role.issues.length > 0)
                  .map((role, index) => (
                    <Card key={index} className="border-l-4 border-red-500">
                      <CardHeader className="pb-2">
                        <div className="flex justify-between items-start">
                          <CardTitle className="text-base">{role.roleName}</CardTitle>
                          <Badge variant={getSeverityVariant(role.issues[0].severity)}>
                            {role.issues[0].severity}
                          </Badge>
                        </div>
                      </CardHeader>
                      <CardContent>
                        <p>{role.issues[0].description}</p>
                        {role.issues.length > 1 && (
                          <p className="text-sm text-gray-600 mt-2">
                            +{role.issues.length - 1} {role.issues.length > 2 ? 'outros problemas' : 'outro problema'}
                          </p>
                        )}
                      </CardContent>
                    </Card>
                  ))}
                {securityData.securityData.roles.every(role => role.issues.length === 0) && (
                  <p className="text-green-600 flex items-center">
                    <CheckCircle className="h-4 w-4 mr-2" />
                    Nenhum problema encontrado com perfis IAM
                  </p>
                )}
              </div>
            </div>
          )}
        </div>
        
        {/* Policies section */}
        <div className="border rounded-lg overflow-hidden">
          <button
            className="w-full p-4 bg-gray-50 flex justify-between items-center"
            onClick={() => toggleSection('policies')}
          >
            <div className="flex items-center space-x-2">
              <FileText className="h-5 w-5" />
              <h3 className="text-lg font-medium">Políticas IAM</h3>
              {securityData.securityData.policies && securityData.securityData.policies.some(policy => policy.issues.length > 0) && (
                <Badge variant="destructive" className="ml-2">
                  {securityData.securityData.policies.filter(policy => policy.issues.length > 0).length}
                </Badge>
              )}
            </div>
            <ChevronDown className={`h-5 w-5 transition-transform ${expandedSections.policies ? 'rotate-180' : ''}`} />
          </button>
          {expandedSections.policies && (
            <div className="p-4">
              <div className="space-y-4">
                {securityData.securityData.policies
                  .filter(policy => policy.issues.length > 0)
                  .map((policy, index) => (
                    <Card key={index} className="border-l-4 border-red-500">
                      <CardHeader className="pb-2">
                        <div className="flex justify-between items-start">
                          <CardTitle className="text-base">{policy.policyName}</CardTitle>
                          <Badge variant={getSeverityVariant(policy.issues[0].severity)}>
                            {policy.issues[0].severity}
                          </Badge>
                        </div>
                      </CardHeader>
                      <CardContent>
                        <p>{policy.issues[0].description}</p>
                        {policy.issues.length > 1 && (
                          <p className="text-sm text-gray-600 mt-2">
                            +{policy.issues.length - 1} {policy.issues.length > 2 ? 'outros problemas' : 'outro problema'}
                          </p>
                        )}
                      </CardContent>
                    </Card>
                  ))}
                {securityData.securityData.policies.every(policy => policy.issues.length === 0) && (
                  <p className="text-green-600 flex items-center">
                    <CheckCircle className="h-4 w-4 mr-2" />
                    Nenhum problema encontrado com políticas IAM
                  </p>
                )}
              </div>
            </div>
          )}
        </div>
        
        {/* Access Keys section */}
        <div className="border rounded-lg overflow-hidden">
          <button
            className="w-full p-4 bg-gray-50 flex justify-between items-center"
            onClick={() => toggleSection('accessKeys')}
          >
            <div className="flex items-center space-x-2">
              <Key className="h-5 w-5" />
              <h3 className="text-lg font-medium">Chaves de Acesso</h3>
              {securityData.securityData.accessKeys && securityData.securityData.accessKeys.some(key => key.issues.length > 0) && (
                <Badge variant="destructive" className="ml-2">
                  {securityData.securityData.accessKeys.filter(key => key.issues.length > 0).length}
                </Badge>
              )}
            </div>
            <ChevronDown className={`h-5 w-5 transition-transform ${expandedSections.accessKeys ? 'rotate-180' : ''}`} />
          </button>
          {expandedSections.accessKeys && (
            <div className="p-4">
              <div className="space-y-4">
                {securityData.securityData.accessKeys
                  .filter(key => key.issues.length > 0)
                  .map((key, index) => (
                    <Card key={index} className="border-l-4 border-red-500">
                      <CardHeader className="pb-2">
                        <div className="flex justify-between items-start">
                          <CardTitle className="text-base">
                            {key.userName} - {key.accessKeyId.substring(0, 8)}...
                          </CardTitle>
                          <Badge variant={getSeverityVariant(key.issues[0].severity)}>
                            {key.issues[0].severity}
                          </Badge>
                        </div>
                      </CardHeader>
                      <CardContent>
                        <p>{key.issues[0].description}</p>
                        <p className="text-sm text-gray-600 mt-1">
                          Criada em: {new Date(key.createDate).toLocaleDateString()}
                        </p>
                        {key.issues.length > 1 && (
                          <p className="text-sm text-gray-600 mt-2">
                            +{key.issues.length - 1} {key.issues.length > 2 ? 'outros problemas' : 'outro problema'}
                          </p>
                        )}
                      </CardContent>
                    </Card>
                  ))}
                {securityData.securityData.accessKeys.every(key => key.issues.length === 0) && (
                  <p className="text-green-600 flex items-center">
                    <CheckCircle className="h-4 w-4 mr-2" />
                    Nenhum problema encontrado com chaves de acesso
                  </p>
                )}
              </div>
            </div>
          )}
        </div>
        
        {/* Account section */}
        <div className="border rounded-lg overflow-hidden">
          <button
            className="w-full p-4 bg-gray-50 flex justify-between items-center"
            onClick={() => toggleSection('accountSettings')}
          >
            <div className="flex items-center space-x-2">
              <Shield className="h-5 w-5" />
              <h3 className="text-lg font-medium">Conta</h3>
              {securityData.securityData.accountIssues && securityData.securityData.accountIssues.length > 0 && (
                <Badge variant="destructive" className="ml-2">
                  {securityData.securityData.accountIssues.length}
                </Badge>
              )}
            </div>
            <ChevronDown className={`h-5 w-5 transition-transform ${expandedSections.accountSettings ? 'rotate-180' : ''}`} />
          </button>
          {expandedSections.accountSettings && (
            <div className="p-4">
              <div className="space-y-4">
                {securityData.securityData.accountIssues.map((issue, index) => (
                  <Card key={index} className="border-l-4 border-red-500">
                    <CardHeader className="pb-2">
                      <div className="flex justify-between items-start">
                        <CardTitle className="text-base">Problema de Conta</CardTitle>
                        <Badge variant={getSeverityVariant(issue.severity)}>
                          {issue.severity}
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <p>{issue.description}</p>
                    </CardContent>
                  </Card>
                ))}
                {!securityData.securityData.accountIssues || securityData.securityData.accountIssues.length === 0 && (
                  <p className="text-green-600 flex items-center">
                    <CheckCircle className="h-4 w-4 mr-2" />
                    Nenhum problema encontrado com a conta
                  </p>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
